*, *::before, *::after{
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

:root{
    --add-food-item-size: 50px;
    --delete-food-item-button-size: 30px;
    --mark-size: calc(var(--add-food-item-size) * 0.9);
    --input-font-size: 1.15rem;
    --input-border-radius: 10px;
    --input-width: 85%;
    --suggestion-list-padding: 10px;
    --body-margin: 8px;
    --border-color-destop: rgba(163, 163, 163, 0.507);
    --margin-destop: 12px;
    --margin-left-destop: 16px;
    --destop-width: 650px;
    --hide-suggestion-list-button-size: 20px;
    --blue-color: rgb(0, 124, 226);
}

body{
    margin: var(--body-margin);
}

.food-item-ke-label{
    float: right;
}

.hide-suggestion-list-button{
    margin: 0;
    float: right;
    position: relative;
    width: var(--hide-suggestion-list-button-size);
    height: var(--hide-suggestion-list-button-size);
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
}

.hide-suggestion-list-button::before,
.hide-suggestion-list-button::after{
    content:"";
    background-color: rgb(146, 146, 146);
    position: absolute;
    width: calc(var(--hide-suggestion-list-button-size) * 0.09);
    height: calc(var(--hide-suggestion-list-button-size) * 0.65);
}

hr,
.destop-hr{
    border: none;
    height: 1px;
    background-color: var(--border-color-destop);
    margin-top: 20px;
    margin-bottom: 17px; 
}

.destop-hr{
    display: none;
}

.slides-container{
    width: 200%;
}

.per-100g-slide-container,
.per-piece-slide-container{
    float: left;
    width: 50%;
}

.per-piece-slide-container{
    transform: translateX(10%);
}


/* SWIPER */
.swiper-container{
    
}

.swiper-wrapper{
    padding: 0;
}

.swiper-slide{
    padding-bottom: 10px;
}

.suggestion-list{
    margin: 0;
    position: absolute;
    padding: var(--suggestion-list-padding);
    list-style: none;
    width: calc(var(--input-width) - 2 * var(--body-margin));
    border-radius: var(--input-border-radius);
    background-color: rgb(177, 177, 177);
    z-index: 999;
}

li{
    margin: 0;
    padding-top: 5px;
    padding-bottom: 5px;
}

h1{
    text-align: center;
    margin-top: 25px;
    margin-bottom: 25px;
}

h3{
    font-size: calc(var(--input-font-size) * 1.2);
}

input[type='text'],
input[type='number']{
    padding: 4px;
    padding-left: 8px;
    width: var(--input-width);
    margin: 0;
    margin-right: 5px;
    font-size: var(--input-font-size);
    font-weight: bold;
    border: 1px solid rgba(0, 0, 0, 0.4);
    outline: none;
    border-radius: var(--input-border-radius);
}

input[type='text']:focus,
input[type='number']:focus{
    border-color: transparent;
    box-shadow: 0 0 1px 2px rgb(228, 148, 0);
}

.intermeal-checkbox input {
    margin: 0;
    width: auto;
    -ms-transform: translate(0%, 20%);
    transform: translate(0%, 20%);
}       

label{
    font-size: calc(var(--input-font-size) * 0.7);
    color: grey;
}

.output{
    font-size: var(--input-font-size);
}

.calculateIE-button, 
.save-data-button,
.load-data-button,
.clear-data-button{
    font-size: calc(var(--input-font-size) * 1.2);
    padding: 10px;
    padding-left: 12px;
    padding-right: 12px;
    border: 1px solid black;
    border-radius: 8px;
    position: relative;
    /* -ms-transform: translate(0%, -50%);
    transform: translate(0%, -50%); */
    margin-right: 10px;
}

.calculateIE-button:active,
.save-data-button:active,
.load-data-button:active,
.clear-data-button:active{
    background-color: rgb(211, 210, 210);
}

/* .calculateIE-button{
    
    -ms-transform: translate(-50%, 0%);
    transform: translate(-50%, 0%);
} */

.delete-food-item-button{
    margin: 0;
    float: right;
    position: relative;
    width: var(--delete-food-item-button-size);
    height: var(--delete-food-item-button-size);
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
}

/* 
*  DAY TIME CHOICE BUTTONS
*/

.day-time-choice-buttons > button{
    border: 1px solid var(--border-color-destop);
    padding: 7px;
    background-color: whitesmoke;
    margin-top: 17px;
    margin-bottom: 11px;
    margin-right: -1px;
}

.automatic-day-time-choice-button{
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding-left: 10px !important;
}

.evening-day-time-choice-button{
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    padding-right: 10px !important;
}


.per-100g-button,
.per-piece-button{
    border: 1px solid var(--border-color-destop);
    border-radius: 7px;
    padding: 7px;
    background-color: whitesmoke;
    margin-bottom: 5px;
}

.per-100g-button{
    margin-right: 15px;
}

.active-button{
    background-color: var(--blue-color) !important;
    color: white !important;
}

.center-elements{
    align-items: center;
    justify-content: center;
    display: flex;
    margin: 0;
}

.delete-food-item-button::before,
.delete-food-item-button::after{
    content:"";
    background-color: black;
    position: absolute;
    width: calc(var(--delete-food-item-button-size) * 0.09);
    height: calc(var(--delete-food-item-button-size) * 0.65);
}

.delete-food-item-button::before,
.hide-suggestion-list-button::before{
    transform: rotate(45deg);
}

.delete-food-item-button::after,
.hide-suggestion-list-button::after{
    transform: rotate(-45deg);
}

.add-new-food-item-button{
    position: relative;
    left: 50%;
    -ms-transform: translate(-50%, 0%);
    transform: translate(-50%, 0%);
    width: var(--add-food-item-size);
    height: var(--add-food-item-size);
    border-radius: 50%;
    /* border: 1px solid black; */
    display: flex;
    justify-content: center;
    align-items: center;
    
    border: 0px;
    background-color: var(--blue-color);
    /* box-shadow: 0 0 5px 2px rgba(163, 163, 163, 0.507); */
}

button:focus{
    outline: none
}


.add-new-food-item-button::before, 
.add-new-food-item-button::after{
    content:"";
    background-color: rgb(255, 255, 255);
    position: absolute;
    width: calc(var(--mark-size) * 0.05);
    height: calc(var(--mark-size) * 0.3);
}

.add-new-food-item-button::after{
    transform: rotate(90deg);
}


@media only screen and (min-width: 670px) {
    body {
        margin-top: 30px;
        margin-bottom: 30px;
        margin-left: 180px;
        width: var(--destop-width);
        border: 1px solid var(--border-color-destop);
        border-radius: 7px;
    }

    .destop-hr{
        display: block;
    }

    .new-section-hr{
        margin-top: 30px;
        margin-bottom: 25px;
    }

    .suggestion-list{
        width: 527px;
    }
    
    .space-around{
        margin-left: var(--margin-left-destop);
        margin-right: var(--margin-destop);
        margin-top: var(--margin-destop);
        margin-bottom: var(--margin-destop);
    }

    h3{
        margin-left: var(--margin-left-destop);
    }

    .clear-data-button{
        margin-top: 0px;
        margin-bottom: 0;
    }

    @media only screen and (max-width: 1010px) {
        body{
            margin-left: calc((100% - 650px) / 2)
        }
    }
}


